































import { Component, Prop, Vue } from 'vue-property-decorator';

import AuthenticationService from '../services/AuthenticationService'
import UserService from '../services/UserService'
export default {
  data () {
    return {
      username: '',
      password: '',
      showSnackbar: false,
      position: 'left',
      duration: 4000,
      isInfinity: false,
      snackbarMessage: ''
    }
  },
  methods: {
    // Action on calling login function
    login () {
      AuthenticationService.signin({
        username: this.username,
        password: this.password
      })
      .then( function (resp) {
        this.$router.push({name: 'userDash', params: {username: this.username}})
      }.bind(this), function (resp) {
        this.showSnackbar = true
        this.snackbarMessage = 'Invalid username or password'
      }.bind(this))
    },
    register () {
      this.$router.push({name: 'register'})
    },
    isLoggedin () {
      UserService.isLoggedin()
      .then (function (resp) {
        this.$router.push({name: 'userDash', params: {username: resp.data.username}})
      }.bind(this),
      function (resp) {
        this.$router.push({name: 'signin'})
      }.bind(this))
    }
  },
  beforeMount() {
    this.isLoggedin()
  },
}
